body {
  margin: 0;
  padding: 0;
	background-color: hsl(0, 0%, 98%);
	font-family: 'Lora', serif;
	color: hsl(0, 0%, 13%);
}

.page {
	padding: 1vmin;
	max-width: 800px;
	margin: auto;
}

p {
	margin: 3vmin;
}

.big {
	font-size: 6.3vmin;
	font-weight: 700;
	line-height: 1.3;
	margin-bottom: 5vmin;
	-webkit-hyphens: auto;
	    -ms-hyphens: auto;
	        hyphens: auto;
}

.body {
	font-size: 4.33vmin;
	font-weight: 400;
	line-height: 1.4;
}

.last {
	text-align: center;
}

a {
	text-decoration: none;
	color: inherit;
	background: none;
	-webkit-transition: color 300ms ease-out, background-color 300ms ease-out;
	-o-transition: color 300ms ease-out, background-color 300ms ease-out;
	transition: color 300ms ease-out, background-color 300ms ease-out;
  -webkit-box-shadow: inset 0 -1px hsl(0, 0%, 13%);
          box-shadow: inset 0 -1px hsl(0, 0%, 13%);
}

.big a {
  -webkit-box-shadow: inset 0 -1.5px hsl(0, 0%, 13%);
          box-shadow: inset 0 -1.5px hsl(0, 0%, 13%);
}

a:hover {
	background: hsl(0, 0%, 13%) !important;
	color: hsl(0, 0%, 98%) !important;
}

::-moz-selection {
	background: hsl(351, 85%, 82%);
}

::selection {
	background: hsl(351, 85%, 82%);
}



